import {
	getArchivesDetail
} from '@/api/job'
import parser from "@/components/jyf-Parser/index"
export default {
	components: {
	   parser
	 },
	data() {
		return {
			detail: null,
		}
	},
	onLoad(e) {
		this.getArchivesDetail(e.id)
	},
	methods: {
		getArchivesDetail(id) {
			getArchivesDetail({
				id
			}).then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.detail = r.detail
					uni.setNavigationBarTitle({
					    title: r.detail.title
					});
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
					setTimeout(() => {
						uni.navigateBack();
					}, 1500)
				}
			})
		}
	}
}
